* {
  box-sizing: border-box;
}

body {
  background-color: #f5f5f5;
  margin: 0;
  padding: 20px;
}

.is-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

img {
  max-width: 100%;
  border: 1px solid #ccc;
  display: block;
}

button {
  cursor: pointer;
  height: 36px;
  width: 120px;
  color: #fff;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  transition: background-color .3s;
  display: flex;
}

button:hover, button:focus {
  background-color: #2980b9;
}

.search-form {
  width: 100%;
  z-index: 999;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  gap: 32px;
  padding: 24px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, .25), 0 8px 16px -8px rgba(0, 0, 0, .3);
}

input {
  height: 36px;
  width: 320px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 100px;
  display: flex;
}

.photo-card {
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  display: block;
}

.photo-card .info {
  color: #555;
  padding: 15px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  border-radius: 5px;
  margin: 30px auto;
  padding: 10px 20px;
  transition: background-color .3s;
  display: flex;
}

.load-more:hover {
  background-color: #27ae60;
}

